<style lang="scss">

  @import "../../styles/utils/palette";
  @import "../../styles/utils/variables";

  .blob-size-switcher {
    margin-left: auto;
    display: flex;
    align-items: center;
    & span.information {
      font-size: 0.9rem;
      margin-right: 20px;
    }
    & .blob-size-box {
      padding: 20px 30px;
      background: $whitesmoke;
      border-radius: $border_radius;
      & svg.icon {
        margin-right: 10px;
        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  svg.action {
    cursor: pointer;
  }

  svg.icon {
    transition: all $transition_duration ease-in;
    color: $shark_dark_grey;
    &:hover {
      color: lighten($pale_green, 10%);
    }
    &.small {
      width: 10px;
      height: 10px;
    }
    &.medium {
      width: 15px;
      height: 15px;
    }
    &.large {
      width: 24px;
      height: 24px;
    }
    &.active {
      color: $pale_green;
      fill: currentColor
    }
  }

  body.theme-light {
    & svg.icon {
      color: $whitesmoke;
      &:hover {
        color: lighten($pale_green, 10%);
      }
      &.active {
        color: $pale_green;
        fill: currentColor
      }
    }
    & .blob-size-switcher .blob-size-box {
      background: $shark_dark_grey;
    }
  }


</style>



